export default {
  head: {
    title: '文章爬取',
    titleTemplate: '%s - DJL箫氏的博客!',
    htmlAttrs: {
      lang: 'en',
      amp: true
    },
    meta: [
      {
        hid: 'description',
        name: 'description',
        content:
          '这是DJL箫氏的博客的文章爬取页面，用来爬取掘金、GitHub、简书等平台文章'
      }
    ]
  },
  components: {},
  data() {
    return {
      templateOptions: [
        { label: '掘金', value: 1 },
        { label: 'Github', value: 2 },
        { label: '简书', value: 3 }
      ],
      ruleForm: {
        url: '',
        template: 1
      },
      rules: {
        url: [
          { required: true, message: '请输入待提取文章链接', trigger: 'blur' }
        ]
      }
    };
  },
  mounted() {},
  methods: {
    formValidate() {
      if (this.$refs.form) {
        this.$refs.form.validate((valid) => {
          if (valid) {
            this.crwalArticle();
          } else {
            return false;
          }
        });
      }
    },
    crwalArticle() {
      const { response } = this.$http({
        url: `/crawl`,
        method: 'get',
        data: {
          template: this.ruleForm.template,
          articleUrl: this.ruleForm.url
        },
        loading: '.app-body',
        showSuccessMsg: false,
        showErrorMsg: true
      });

      return response.then(
        (data) => {
          this.$router.push(`/post/${data._id}`);
        },
        () => {}
      );
    }
  }
};
